.imagen-contacto{
    background-image: url('/build/img/encuentra.jpg');
}

.imagen-contacto{
    background-position: center center;
    background-size: cover;
    margin-top: 5rem;
    padding: $separacion * 2 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    h2{
        font-size: 4rem;
        font-weight: $black;
        color: $blanco;
    }
    p{
        font-size: 1.8rem;
        color: $blanco;
    }
}