@include tablet{
    .iconos-nosotros{
        @include grid(3, 2rem);
    }
}

.icono {
    text-align: center;
    img{
        height: 17rem;
    }
    h3{
        text-transform: uppercase;
    }
    h1{
        font-weight: $regular;
    }
    p{
        font-size: 2rem;
    }
}



