.dark-mode {
  background-color: #282728;
}
.dark-mode td {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5rem;
}
.dark-mode p, .dark-mode td {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  text-align: justify;
  background-color: #414042;
  border: 1px solid #414042;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #FFFFFF;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .resumen-anuncio {
  text-align: justify;
}
.dark-mode .resumen-anuncio p {
  font-weight: 700;
}
.dark-mode .descripcion-anuncio {
  text-align: justify;
  font-weight: 300;
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label, .dark-mode .formulario legend, .dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #414042;
  border: 1px solid #414042;
  color: #FFFFFF;
}
.dark-mode .credito {
  color: #71B100;
}
.dark-mode .resumen-blog {
  text-align: justify;
}
.dark-mode .resumen-blog p {
  font-size: 2rem;
  font-weight: 100;
}
.dark-mode .resumen-blog li {
  font-size: 2rem;
  margin: 0;
  color: #FFFFFF;
}
.dark-mode .resumen-blog ul {
  list-style-type: decimal;
  margin: 0;
}
.dark-mode .resumen-blog span {
