.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet {
        align-items: flex-end;
    }

    .dark-mode-boton{
        width: 3rem;
        margin-top: 3rem;
        margin-bottom: 2rem;
        filter: invert(100%);

        &:hover {
            cursor: pointer;
        }
    }
}

.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 2rem;
    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}
.navegacion {

    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .2s ease-in-out, opacity .3s linear, height .5s linear;
    @include tablet{
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }

    a {
    color: $blanco;
    text-decoration: none;
    font-size: 2.2rem;
    display: block;
    text-align: center;


    @include tablet{
        margin-right: 2rem;
        font-size: 1.8rem;
        &:last-of-type{
            margin-right: 0;
        }
    }
    &:hover{
        color: $rojo;
    }
    }
}


/** Menu de hamburguesa**/
.mobile-menu{
    @include tablet{
        display: none;
    }
    img{
        width: 5rem;

    }
}
.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}